/* CSS for Messaging App */
.messaging-app-container {
    max-width: 600px;
    margin: auto;
    padding: 20px;
  }
  
  .app-title {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .error {
    color: red;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .message-input-container {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .message-input {
    margin-left: 10px;
    font-size: 16px;
    padding: 10px;
    border: none;
    border-radius: 20px;
  }
  
  .send-button {
    margin-bottom: 10px;
    margin-left: 10px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    animation: slideInRight 0.5s ease;
  }
  
  .message-list ul {
    list-style: none;
    padding: 0;
  }
  
  .message {
    margin-bottom: 10px;
    padding: 10px 20px;
    border-radius: 10px;
    max-width: 70%;
    animation: fadeIn 0.5s ease;
  }
  
  .message.sent {
    background-color: #dcf8c6;
    align-self: flex-end;
  }
  
  .message.received {
    background-color: #eaeaea;
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes slideInRight {
    from {
      opacity: 0;
      transform: translateX(20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }