#profile-container {
    max-width: 600px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
  }
  
  .error {
    color: red;
    margin-bottom: 20px;
  }
  
  .profile-info {
    margin-top: 20px;
  }
  
  .highlight {
    color: red;
  }
  
  .search-button {
    padding: 10px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
  }
  